
body
  background-color: #ffffff
  margin: 0

div
  background-color: black
  
.main-wrapper
  background-color: #ffffff
  display: flex
  justify-content: center
  user-select: none
  align-items: center
  height: 100vh
  width: 100vw
  
.main-container
  padding: 6px
  border-radius: 6px
  box-shadow: 0 0 2vh #000000
  .color-select
    display: flex
    margin-bottom: 6px
    .select
      display: flex
      justify-content: center
      align-items: center
      width: 8vh
      height: 4vh    
    .highlight
        box-shadow: 0 0 2vh #ffffff
    .color
      border-radius: 50%
      border: 0px solid white
      height: 3vh
      width: 3vh
  .color-grid
    display: grid
    grid-template-columns: 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 
    grid-template-rows: 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 4vh 
    .grsq
      display: flex
      justify-content: center
      align-items: center      
    .grid
      margin: auto
      height: 2.5vh
      width: 2.5vh
      border-radius: 50%
      border: 0px solid white
      box-shadow: 0 0 1vh #ffffff
